import React, { useState, useEffect } from 'react';
import { withStyles, useTheme } from '@material-ui/styles';
import { IconSearch } from '@tabler/icons';
import GroupIcon from '@material-ui/icons/Group';
import useStyles from '../../styles';
import {
    IconButton,
    Typography,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip,
    Grid,
   
} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';

import useUserRole from '../../../../hooks/useUserRole';
import ArrowForwardIcon from '@material-ui/icons/ArrowUpward';
import '../../../../notification/Notification.css';
import MainCard from '../../../../ui-component/cards/MainCard';
import { deleteUserProfile, getAllDeletedProfile } from '../../../../store/actions/AddUserActions';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);
export default function DeletedUsers() {
  const theme = useTheme();

  const classes = useStyles();
  useUserRole({ roleList: ['ADMIN', 'SCHOOL_ADMIN','SUPER_ADMIN'] });
  const dispatch = useDispatch();
 
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(25);
 

 

  const [isSearchUser, setSearchUser] = useState('');
  const { getAllProfileData } = useSelector((state) => state?.addUser);
 
  const handleChangePage = (event, newPage) => {
      setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
      setRowsPerPage(+event.target.value);
  };

  
  const handleSearchUser = (event) => {
      setSearchUser(event.target.value);
  };
  const handleActiveuserProfile = (id) => {
    const payload = {
      isDeleted: false
    };
    dispatch(
      deleteUserProfile(payload, id, () => {
            dispatch(getAllDeletedProfile(rowsPerPage, isSearchUser ? isSearchUser : '', page + 1));
        })
    );
};
  useEffect(() => {
      dispatch(getAllDeletedProfile(rowsPerPage, isSearchUser ? isSearchUser : '', page + 1));
  }, [rowsPerPage, isSearchUser, page]);

  return (
      <>
          <MainCard>
              <div className={classes.titleContainer}>
                  <GroupIcon className={classes.pageTitleIcon} />
                  <span className={classes.pageTitle}>&nbsp; Bookeinstein all deleted users </span>
              </div>
              <Paper elevation={5} className={classes.root}>
                  <Grid container spacing={3}>
                      <Grid item xs={12} sm={6} md={3}>
                          <OutlinedInput
                              className={classes.searchControl}
                              id="input-search-header"
                              placeholder="Search"
                              onChange={(event) => handleSearchUser(event)}
                              startAdornment={
                                  <InputAdornment position="start">
                                      <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                  </InputAdornment>
                              }
                          />
                      </Grid>
                  </Grid>
                  <TableContainer className={classes.tableContainer}>
                      <Table sx={{ minWidth: 200 }} className={classes.table} aria-label="simple table">
                          <TableHead className={classes.tableHeaderCell}>
                              <StyledTableRow>
                                  <TableCell> Email </TableCell>
                                  <TableCell> Role </TableCell>
                                  <TableCell> Mobile </TableCell>
                                  <TableCell align="center"> Status </TableCell>
                                  <TableCell align="center"> Action </TableCell>
                              </StyledTableRow>
                          </TableHead>
                          {!getAllProfileData ? (
                              <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                          ) : (
                              <TableBody>
                                  {getAllProfileData?.content?.map((row) => (
                                      <StyledTableRow key={row?.id}>
                                          <TableCell> {row?.email} </TableCell>
                                          <TableCell> {row?.role.replace('_', ' ')} </TableCell>
                                          <TableCell> {row?.mobile || '-'} </TableCell>
                                          <TableCell align="center">
                                              <Typography
                                                  className={classes.status}
                                                  style={{
                                                      backgroundColor:
                                                          row?.status === 'ACTIVE' ? theme.palette.success.main : theme.palette.warning.dark
                                                  }}
                                              >
                                                  {row?.status}
                                              </Typography>
                                          </TableCell>
                                          <TableCell align="center">
                                              <Tooltip title="Restore User ">
                                                  <IconButton
                                                      onClick={() => {
                                                          handleActiveuserProfile(row.id);
                                                      }}
                                                  >
                                                      <ArrowForwardIcon color="primary" style={{ cursor: 'pointer' }} />
                                                  </IconButton>
                                              </Tooltip>
                                          </TableCell>
                                      </StyledTableRow>
                                  ))}
                              </TableBody>
                          )}
                      </Table>
                  </TableContainer>
                  <TablePagination
                      labelRowsPerPage={<span>Rows:</span>}
                      labelDisplayedRows={({ from, to, count }) => {
                          return `Page: ${page + 1} (${from}-${to} of ${count})`;
                      }}
                      backIconButtonProps={{
                          color: 'secondary'
                      }}
                      nextIconButtonProps={{ color: 'secondary' }}
                      SelectProps={{
                          inputProps: {
                              'aria-label': 'page number'
                          }
                      }}
                      showFirstButton={true}
                      showLastButton={true}
                      rowsPerPageOptions={[25, 50, 100]}
                      component="div"
                      count={getAllProfileData?.totalElements || 0}
                      rowsPerPage={rowsPerPage}
                      page={page}
                      onPageChange={handleChangePage}
                      onRowsPerPageChange={handleChangeRowsPerPage}
                      sx={{
                          '.MuiTablePagination-displayedRows': {
                              marginBottom: 0
                          },
                          '.MuiTablePagination-selectLabel': {
                              marginBottom: 0
                          }
                      }}
                  />
              </Paper>
          </MainCard>
      </>
  );
}
